<template>
  <div class="wrapper" v-show="isShowSearch">
    <div class="header">
      <div class="icon-wrapper">
        <i class="back" @click="back"></i>
      </div>
      <div class="search-box">
        <SearchBox @query="onQueryChange" @focus="focus"></SearchBox>
      </div>
      <div class="text-wrapper" @click="back">
        <span>取消</span>
      </div>
    </div>
    <div class="content-wrapper">
      这里是原料首页
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import SearchBox from 'base/search-box/search-box'

  export default {
    data () {
      return {
        query: ''
      }
    },
    props: {
      isShowSearch: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      back() {
        this.$emit('hide')
      },
      focus() {
        console.log('xxx')
      },
      onQueryChange(newQuery) {
        this.query = newQuery
        //dosomething
      }
    },
    components: {
      SearchBox
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .icon-wrapper
        width 50px
        height 100%
        .back
          position absolute
          background url("~common/image/back.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .search-box
        display flex
        flex 1
      .text-wrapper
        width 50px
        height 100%
        display flex
        align-items center
        justify-content center
        color #3d3d3d
        font-size $font-size-medium
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      justify-content center

</style>



