<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <div class="icon-wrapper">
          <i class="back" @click="back"></i>
        </div>
        <div class="search-box">
          <SearchBox @query="onQueryChange" @focus="showSearch"></SearchBox>
        </div>
        <div class="icon-wrapper">
          <i class="icon-category" @click="showCategory"></i>
        </div>
      </div>
      <div class="content-wrapper">
        这里是原料首页
      </div>
      <search :isShowSearch="isShowSearch" @hide="hideSearch"></search>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import SearchBox from 'base/search-box/search-box'
  import Search from 'components/purchase/search'

  export default {
    data () {
      return {
        query: '',
        isShowSearch: false
      }
    },
    methods: {
      back() {
        this.$router.back()
      },
      showSearch() {
        this.isShowSearch = true
      },
      hideSearch() {
        this.isShowSearch = false
      },
      showCategory() {

      },
      onQueryChange(newQuery) {
        this.query = newQuery
        //dosomething
      }
    },
    components: {
      SearchBox,
      Search
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active,.slide-leave-active
    transition all 0.3s
  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .icon-wrapper
        width 50px
        height 100%
        .back
          position absolute
          background url("~common/image/back.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .search-box
        display flex
        flex 1
      .icon-wrapper
        .icon-category
          position absolute
          background url("~common/image/purchase/category.png") no-repeat
          background-size contain
          width 14px
          height 13px
          right 15px
          top 16px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      justify-content center

</style>



